.intro{position:relative;display:flex;align-items:flex-start;justify-content:left;height:40vh;color:#fff;padding:30px 0;overflow:hidden}@media(min-width: 768px){.intro{padding:60px 0;height:35vh}}.intro__background{position:absolute;top:0;left:0;width:100%;height:100%;background:url("../../assets/intro-mobile.webp") center/cover no-repeat;filter:brightness(0.5)}@media(min-width: 768px){.intro__background{background:url("../../assets/intro-desktop.webp") center/cover no-repeat}}.intro__content{position:relative;display:flex;flex-direction:column;justify-content:center;text-align:left;align-items:flex-start;padding:0 16%;max-height:100vh;overflow-y:auto;scroll-behavior:smooth;transform:translateY(20px);opacity:1;transform:translateY(0)}@media(min-width: 768px){.intro__content{align-items:center;text-align:center}}@media(min-width: 1068px){.intro__content{gap:20px}}.intro__title{margin:0;font-family:"Geometria Medium";font-size:52px;line-height:1em;padding-bottom:30px;text-align:left;letter-spacing:.1em}